import React from "react";
import { Grid } from "@chakra-ui/react";
import "../../styles/scss/main.scss";
import "../../styles/scss/cards.scss";
import { useState } from "react";
import { useEffect } from "react";
import Slider from "./Slider";
import EntradasRecientes from "./EntradasRecientes";
import Loader from "./Loader";
import VisitaExploraEnCasa from "./VisitaExploraEnCasa";

export default function Home() {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    getData();
  }, []);

  //Traer la data desde la API
  const getData = async () => {
    try {
      const resp = await fetch(
        "https://encasa.parqueexplora.org/wp-json/wp/v2/posts"
      );
      const inData = await resp.json();

      //Guarda la data relevante en el state data
      inData.forEach((d) => {
        const summary = d.excerpt?.rendered?.replace(/<[^>]+>/g, "");
        const newData = {
          id: d.id,
          title: d.title?.rendered,
          summary,
          url: d.guid?.rendered,
          img: d.jetpack_featured_media_url,
          catagory: d.categories,
        };
        setData((data) => [...data, newData]);
      });

      //Establece que ya se terminó de cargar la data
      setLoading(false);
    } catch {
      console.warn("No se recibió la data");
    }
  };

  return (
    <div style={{ backgroundColor: "#a569d1", boxSizing: "border-box" }}>
      {/* --------------------- Sección 1 ---------------- */}
      <div className="container-fluid p-0">
        <Grid
          display="inline-flex"
          flexDirection="column"
          minHeight="100%"
          justifyContent="space-evenly"
          w="100%"
        >
          {loading ? (
            <Loader />
          ) : (
            <div className="animate__animated animate__bounceInDown">
              <div className="separator" />
              {/* ------------------------- Slider ---------------------- */}
              <Slider data={data} />
              <div className="separator" />
              {/* --------------------Entradas recientes ---------------- */}
              <EntradasRecientes data={data} />
              <div className="separator" />
              {/* ------------------------ Slider ----------------------- */}
              <VisitaExploraEnCasa data={data} />
              <div className="separator" />
            </div>
          )}
        </Grid>
      </div>
    </div>
  );
}
