import React from 'react'
import styled from 'styled-components';
import { Box } from "@chakra-ui/react";

//estilos locales del slider
const Title = styled.h1`
  color: #eeeeee;
  font-size: 3rem;
  font-family: "Open Sans";
  width: 100%;
  text-align: center;
`;


export default function EntradasRecientes({data}) {
    return (
        <>
             <Title>Entradas recientes</Title>
              <Box
                fontWeight="900"
                textAlign="center"
                width="100%"
                color="white"
                style={{ fontFamily: "Exo2", width: "100%" }}
                className=""
                display="flex"
                alignItems="center"
                justifyContent="space-evenly"
                flexWrap="wrap"
              >
                {data?.map(
                  ({ title, url, img, summary, id }, i) =>
                    i < 3 && (
                      <a href={url} target="_blank" rel="noreferrer" key={id}>
                        <span>
                          <div>
                            <div className="card">
                              <figure>
                                <img src={img} alt={title} />
                              </figure>
                              <div>
                                <h1>{title}</h1>
                                <p>{summary}</p>
                              </div>
                              Ver mas
                            </div>
                          </div>
                        </span>
                      </a>
                    )
                )}
              </Box>
        </>
    )
}
